.canvas-container {
    position: relative;
    width: 90vw;  /* Takes 90% of viewport width */
    height: 90vh; /* Takes 90% of viewport height */
    margin: 0 auto;
}

.canvas-overlay {
    position: absolute;
    top: 1rem;
    right: 1rem;
    z-index: 10; /* Ensure it's above all canvases */
    background: rgba(0, 0, 0, 0.7);
    padding: 1rem;
    border-radius: 8px;
    border: 1px solid var(--border-color);
}

.canvas-overlay button {
    background: var(--accent-color);
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.canvas-overlay button:hover {
    background: var(--accent-hover);
}

canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Optional: Different z-index for each canvas if you need specific stacking order */
.grid-canvas { z-index: 1; }
.arrow-canvas { z-index: 2; }
.particle-canvas { z-index: 3; }
.equipotential-canvas { z-index: 4; }