/* Global Styles */
.dark-theme {
  --bg-primary: #1e1e1e;      /* Dark background */
  --bg-secondary: #000000;    /* Slightly lighter background */
  --text-primary: #ffffff;    /* Light blue-gray for main text */
  --text-secondary: #c0c0c0;  /* Darker blue-gray for secondary text */
  --accent-color: #f07838;    /* Bright orange for emphasis */
  --accent-hover: #ba4c40;    /* Darker orange for hover states */
  --hover-bg: #2a2d2e;        /* Slightly lighter than bg-secondary */
  --border-color: #22262d;    /* Blue-gray for borders */
  --highlight-color: #f5c9a8; /* Light orange for subtle highlights */
}

.App {
  background-color: var(--bg-primary);
  color: var(--text-primary);
  min-height: 100vh;
  height: fit-content;
  position: relative;
  z-index: 0; /* Place below content */
  overflow-x: hidden;
  width: 100%;
}

/* Navigation Bar */
.navbar {
  background-color: var(--bg-secondary);
  padding: 0.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  border-bottom: 1px solid var(--border-color);
  height: 3.5rem;
  box-sizing: border-box;
}

.nav-brand {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--accent-color);
  content: 'zgunther';
  transition: color 0.3s ease;
}

.nav-brand a {
  color: var(--accent-color);
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-brand a:hover,
.nav-brand a:visited,
.nav-brand a:active,
.nav-brand a:focus {
  color: var(--accent-hover);
  text-decoration: none;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 2rem;
  margin: 0;
  padding: 0;
}

.nav-links a {
  color: var(--text-primary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: var(--accent-color);
}

/* Main Content Sections */
main {
  padding-top: 1rem;
}

section {
  min-height: 70vh; /* Reduce from 100vh to show next section */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
}

/* Hero Section */
.hero-section {
  background-color: transparent; /* Remove background color */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 2rem 1rem 2rem;
  position: relative;
  z-index: 1; /* Ensure content is above background */
}

.hero-content {
  display: flex;
  align-items: center;
  gap: 2rem;
  max-width: 1200px;
  padding: 0 2rem;
  position: relative;
  z-index: 2; /* Ensure above background */
  /* background: rgba(30, 30, 30, 0.3); Semi-transparent background */
  /* backdrop-filter: blur(5px); /* Blur effect behind content */
  border-radius: 20px;
  padding: 2rem;
}

.hero-text {
  text-align: left;
  opacity: 0;
  margin-left: 450px;
  animation: fadeInRight 1s ease-in forwards;
  animation-delay: 2s;
  width: 100%;
  max-width: 600px;
}

.hero-text h1 {
  font-size: 3.5rem;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.about-text {
  margin-top: 1.5rem;
  line-height: 1.6;
  font-size: 1.1rem;
  color: var(--text-secondary);
  max-width: 500px;
}

/* Add these new styles for the projects section */
.projects-section {
  position: relative;
  padding: 1rem 1rem 4rem 1rem;
  /* background-color: rgb(0, 0, 0); */
  min-height: auto;
  width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  /* background: linear-gradient(to bottom, var(--accent-color), rgba(0,0,0,1)); */
  background: black;
  /* -webkit-background-clip: text; */
  /* -webkit-text-fill-color: transparent; */
}

.projects-section::before {
  content: '';
  position: absolute;
  top: -50px;
  left: 0;
  right: 0;
  height: 1px;
  /* background: var(--bg-secondary); */
  /* clip-path: polygon(0 100%, 100% 100%, 100% 0, 50% 100%, 0 0); */
  /* Gradient from gray to black top top bottom of section */
}

.projects-section h2 {
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 3rem;
  color: var(--text-primary); /* Make text visible */
  position: relative;
  display: inline-block;
}

/* Optional: Add a subtle text shadow for better visibility */
.projects-section h2 {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.projects-section h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 4px;
  /* background: linear-gradient(90deg, 
    transparent, 
    var(--accent-color), 
    var(--highlight-color), 
    transparent
  ); */
  border-radius: 2px;
}

/* Update project grid container */
.project-grid-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  padding: 0 1rem;
  box-sizing: border-box;
}

.project-grid-container::before {
  content: '';
  position: absolute;
  top: -20px;
  left: -20px;
  right: -20px;
  bottom: -20px;
  /* background: linear-gradient(45deg, 
    rgba(240, 120, 56, 0.1), 
    rgba(245, 201, 168, 0.1)
  ); */
  border-radius: 20px;
  z-index: -1;
}

/* Update project grid */
.project-grid {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  width: 100%;
  padding: 1.5rem;
  border-radius: 15px;
  background: rgba(30, 30, 30, 0);
  box-sizing: border-box;
}

.project-card {
  position: relative;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.3s ease;
  aspect-ratio: 16/9;
  text-decoration: none;
  max-height: 30vh; /* Limit height on narrow screens */
  
  box-shadow: 0 0 50px rgba(158, 158, 158, 0.2);
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-image {
  position: relative;
  width: 100%;
  height: 100%;
}

.project-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85); /* Slightly more opaque overlay */
  color: white;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-overlay h3 {
  margin: 0 0 1rem;
  font-size: 1.5rem;
  color: var(--accent-color);
}

.project-overlay p {
  margin: 0;
  font-size: 1rem;
  line-height: 1.5;
  color: var(--text-primary);
}

/* Profile Image */
.profile-image {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  overflow: hidden;
  border: 4px solid var(--accent-color);
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.4);
  opacity: 0;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  animation: fadeInAndSlide 2s ease-in-out forwards;
  z-index: 2; /* Ensure it's above background */
}

.profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: blur(20px);
  animation: unblur 0.3s ease-out forwards;
  animation-delay: 0.1s;
}

/* Add unblur animation */
@keyframes unblur {
  from {
    filter: blur(20px);
  }
  to {
    filter: blur(0);
  }
}

/* Update animations */
@keyframes fadeInAndSlide {
  0% {
    opacity: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  50% {
    opacity: 1;
    left: 50%;
    transform: translateX(-50%);
  }
  100% {
    opacity: 1;
    left: 0;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Update responsive design */
@media (max-width: 1024px) {
  .hero-content {
    gap: 2rem;
  }
  
  .hero-text h1 {
    font-size: 2.8rem;
  }
}

@media (max-width: 768px) {
  .profile-image {
    position: relative;
    left: auto;
    transform: none;
    width: 300px;
    height: 300px;
    animation: fadeIn 1s ease-in forwards;
    margin: 0 auto; /* Center the image */
    z-index: 2; /* Keep it above background */
  }

  .hero-content {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
    width: 100%; /* Ensure full width */
    z-index: 2; /* Keep content above background */
  }

  .hero-text {
    text-align: center;
    margin-left: 0;
  }

  .about-text {
    margin: 1rem auto;
  }

  .profile-image img {
    animation: unblur 1s ease-out forwards;
  }

  .projects-section h2 {
    font-size: 2.5rem;
  }
}

@media (max-width: 480px) {
  .hero-content {
    flex-direction: column;
    text-align: center;
    gap: 1.5rem;
  }

  .profile-image {
    position: relative;
    left: auto;
    transform: none;
    width: 300px;
    height: 300px;
    animation: fadeIn 1s ease-in forwards;
  }

  .hero-text {
    text-align: center;
    margin-left: 0;
  }

  .about-text {
    margin: 1rem auto;
  }

  .profile-image img {
    animation: unblur 1s ease-out forwards;
  }

  .projects-section h2 {
    font-size: 2rem;
  }
  
  .projects-section {
    padding-top: 4rem;
  }
}

/* Resume Page */
.resume-container {
  width: 100%;
  height: calc(100vh - 4rem); /* Subtract navbar height */
  margin-top: 4rem; /* Account for navbar */
  background-color: var(--bg-primary);
  z-index: 1000;
}

.resume-container object {
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

/* Add this to your existing App.css */
.project-page {
  padding-top: 4rem; /* Match navbar height */
  min-height: 100vh;
  height: fit-content;
}

/* Add responsive adjustments for narrow screens */
@media (max-aspect-ratio: 1/1) {
  .project-grid {
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 1rem;
    padding: 0 1rem;
  }

  .project-card {
    max-height: 25vh;
  }

  .project-overlay h3 {
    font-size: 1.2rem;
    margin: 0 0 0.5rem;
  }

  .project-overlay p {
    font-size: 0.9rem;
    line-height: 1.3;
  }
}

/* Add even smaller screen adjustments */
@media (max-width: 480px) {
  .project-grid {
    grid-template-columns: 1fr;
    padding: 0 0.5rem;
  }

  .project-card {
    max-height: 20vh;
  }
}

/* Add division between sections */
.hero-section::after {
  display: none; /* Remove the gradient fade */
}

/* Add to ensure all elements respect border-box */
*, *:before, *:after {
  box-sizing: border-box;
}

/* Experience Section */
.experience-section {
  position: relative;
  padding: 1rem 1rem;
  min-height: auto;
  width: 100%;
  box-sizing: border-box;
  z-index: 1;
  background-color: var(--accent-color);
}

.experience-section h2 {
  color: var(--bg-primary);
  margin-bottom: 2rem;
  margin-top: 1rem;
  font-size: 2.5rem;
  font-weight: 800;
}

.experience-grid {
  display: flex;
  justify-content: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.experience-card {
  flex: 1;
  max-width: 350px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  border-radius: 10px;
  padding: 1.5rem;
  transition: transform 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Update hover effect for better contrast */
.experience-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.experience-card h3 {
  color: var(--accent-color);
  margin: 0 0 0.5rem 0;
  font-size: 1.3rem;
}

.experience-card h4 {
  color: var(--text-secondary);
  margin: 0 0 1rem 0;
  font-size: 1rem;
  font-weight: normal;
}

.experience-card p {
  color: var(--text-primary);
  margin: 0;
  font-size: 0.9rem;
  line-height: 1.4;
}

/* Responsive design for experience cards */
@media (max-width: 900px) {
  .experience-grid {
    flex-direction: column;
    align-items: center;
  }

  .experience-card {
    width: 100%;
    max-width: 500px;
  }
}

/* Update keyword tags styles */
.keyword-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin: 0.5rem 0 1rem 0;
  justify-content: center; /* Center the tags container */
  align-items: center;
}

.keyword-tag {
  background: #000000;
  color: white;
  padding: 0.2rem 0.6rem;
  border-radius: 12px;
  font-size: 0.8rem;
  font-weight: 500;
  white-space: nowrap;
  display: inline-flex; /* Better alignment for tags */
  align-items: center;
  justify-content: center;
}

/* Add fadeIn animation for mobile */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
